import React from 'react';
import {Link} from 'react-router-dom'
import ReviewsForm from '../reviews/reviews_container';

function changeDisplayedImage(src) {
  var expandImg = document.getElementById("expandedImg");
  expandImg.src = src;
  expandImg.parentElement.style.display = "block";
}



class ItemDetails extends React.Component{

  constructor(props){
    super(props)
  }

  componentDidMount(){
    this.props.fetchItem(this.props.match.params.itemId);
  }

  render(){
    let {item, brand, color} = this.props;
    if(!item || !brand && !color){
      return null;
    }
    let photos = JSON.parse(item.photos)
    return(
 
  <div className="container item_detail_container">
        <div className="card item_detail_card">
          <div className=" row">
            <div className=" col-md-6">
             <h3 className="product-title">{brand.name}</h3>
              <div className="rating">
                <div className="stars">
                  <span className="fa fa-star text-warning"></span>
                  <span className="fa fa-star text-warning"></span>
                  <span className="fa fa-star text-warning"></span>
                  <span className="fa fa-star"></span>
                  <span className="fa fa-star"></span>
                </div>
                <span className="review-no">41 reviews</span>
              </div>
              <p className="product-description">{item.description}</p>
              <h4>current price:<span className="price">{item.price}</span></h4>
              <h4>color: <span>{color.name}</span></h4>

              <p className="vote"><strong>91%</strong> of buyers enjoyed this product! <strong>(87 votes)</strong></p>
              <div className="action">
                <a href="#" className="btn btn-info btn-lg">
                  <span className="glyphicon glyphicon-shopping-cart"></span>Add to Cart</a>
                &nbsp;
                <Link to={`/items`} className="btn btn-lg btn-warning">Continue shopping</Link>
              </div>
            </div>

            <div className="col-md-6">
              <div className="container">
                <img id="expandedImg" src={photos[0]} />
              </div>
            </div>

            <div className="row">

              <div className="item_detail_column">
                <img className="img-thumbnail" src={photos[0]}
                  alt="Nature" onClick={() => changeDisplayedImage(`${photos[0]}`)} />
              </div>
              <div className="item_detail_column">
                <img className="img-thumbnail" src={photos[1]}
                  alt="Snow" onClick={() => changeDisplayedImage(`${photos[1]}`)} />
              </div>
              <div className="item_detail_column">
                <img className="img-thumbnail" src={photos[2]}
                  alt="Mountains" onClick={() => changeDisplayedImage(`${photos[2]}`)} />
              </div>
              <div className="item_detail_column">
                <img className="img-thumbnail" src={photos[3]}
                  alt="Lights" onClick={() => changeDisplayedImage(`${photos[3]}`)} />
              </div>

            </div>

          </div>
        </div>

        <ReviewsForm/>
       
      </div> 
    );
  }
}

export default ItemDetails;