import React from 'react';
import ReviewItem from './reviews_item'

class ReviewIndex extends React.Component{
  constructor(props){
    super(props)
  }

  componentDidMount(){
    this.props.fetchReviews(this.props.match.params.itemId);
  }

  render(){
    const reviewItemsList = this.props.reviews.map(review => {
      return (<ReviewItem review={review} key={review.id} />)
  });
    return(
      <div className="container">
        <h2 className=" text-white">check others reviews</h2>
          {reviewItemsList}
      </div>
    );
  }
}

export default ReviewIndex;